// Importando os components necessrios da lib react-materialize
import { Row, Col, Card } from "react-materialize";
// importando a foto do usurio (note que depois podemos usa-la facilmente chamando apenas avatar ao invs do caminho completo)
import avatar from "../../images/avatar.png";

const UserProfile = () => (
  <Card>
    <Row>
      <Col s={8} m={8} offset="s2 m2">
        <img src={avatar} className="circle responsive-img" />
      </Col>
    </Row>
    <Row className="center-align">
      <h5>Lorem Ipsum</h5>
      <p className="grey darken-2 white-text">Ruby Developer</p>
    </Row>
  </Card>
);

export default UserProfile;
